.carousel {
  display: block;
  max-width: 70%;
  align-items: center;
  margin: 0 auto;
}

.error-message {
  color: red;
  font-size: 12px;
}

.header {
  display: flex;
  text-align: left;
  margin-bottom: 36px;
}

.header-items-list {
  padding: 0;
  margin: 0;
}

.header-item {
  display: inline-block;
  margin: 0 40px;
  font-size: 20px;
  font-weight: bold;
}

.indra-name {
  font-size: 32px;
  text-align: center;
  margin: 0 40px 0 0;
}

.margin-bottom-80 {
  margin-bottom: 80px;
}

.margin-bottom-100 {
  margin-bottom: 100px;
}

.margin-top-60 {
  margin-top: 60px;
}

.model-status {
  float: right;
  width: 28rem;
  height: 28rem;
}

.number-input {
  width: 40px;
  height: 30px;
}

.small-header {
  font-size: 16px;
  font-weight: 400;
}

.list-group-item {
  cursor: pointer;
}

.link {
  word-wrap: break-word;
}
